}

.transition-scale {
	position: relative;
	z-index: 2;
	margin: 0 auto;
	text-align: center;
	cursor: pointer;
	transition: 1s linear;
}

.transition-scale:hover {
	-webkit-transform: scale(2);
	transform: scale(1.2);
}

a:hover {
	text-decoration: none;
}

a:visited,  a:active, a:focus {
	text-decoration: none!important;
}

.dropdown {
