//menu line
.ph-line-nav.nav.menu.col-lg-6 {
	padding: 0!important;
}

.nav {
	text-align: center;
	overflow: hidden;
	position: relative;
}
.nav ul li {
	display: block;
	position: relative;
	float: left;
	padding: 3% 0% 2% 0%;
	width: 8%;
	text-decoration: none;
	color: #393939;
	-webkit-transition: .7s;
	-moz-transition: .7s;
	-o-transition: .7s;
	-ms-transition: .7s;
	transition: .7s;
}

.effect {
	position: absolute;
	left: 10%;
	-webkit-transition: 0.7s ease-in-out;
	-moz-transition: 0.7s ease-in-out;
	-o-transition: 0.7s ease-in-out;
	-ms-transition: 0.7s ease-in-out;
	transition: 0.7s ease-in-out;
}

.nav ul li:nth-child(1):hover ~ .effect {
	left: 22.2%;
}
.nav ul li:nth-child(2):hover ~ .effect {
	left: 42%;
}
.nav ul li:nth-child(3):hover ~ .effect {
	left: 61.2%;
}
.nav ul li:nth-child(4):hover ~ .effect {
	left: 81%;
}
.nav ul li:nth-child(5):hover ~ .effect {
	left :101%;
}
.nav ul li:nth-child(6) {
	padding: 0;
	margin-top: 9% !important;
}

.ph-line-nav .effect {
	width: 8%;
	height: 3px;
	bottom: 0;
	background: #fce38a;
	margin-left: -120%;
}

/*-------#story-------*/

