}

.active-s {
	overflow: hidden;
	.text-c;
	opacity: 0;
	left: 0px;
	top: -0.5%;
	width: 100%;
	height: 101%;
	position: absolute;
	padding-top: 29%;

	&:hover {
		opacity: 0.9;
		transition: 0.5s;
		/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#f18f8b+0,f1e767+74 */
		background: rgb(241,143,139); /* Old browsers */
		background: -moz-linear-gradient(top,  rgba(241,143,139,1) 0%, rgba(241,231,103,1) 74%); /* FF3.6-15 */
		background: -webkit-linear-gradient(top,  rgba(241,143,139,1) 0%,rgba(241,231,103,1) 74%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to bottom,  rgba(241,143,139,1) 0%,rgba(241,231,103,1) 74%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
