/*-------accordion-------*/

.accordion-container {
	width: 100%;
	margin: 0 0 3%;
	clear: both;
	border: 1px solid @color-ak-border;
	a:hover {
		text-decoration: none;
		color: @color-icon;
	}

	i {
		color: #d0d0d0;
		font-size: 4rem;
		&:hover {
			color: @color-icon;
		}
	}

	.panel-icon {
		width: 8%;
		float: left;
		overflow: hidden;
		span {
			color: @color-icon;
			font-size: 3rem;
			display: block;
			padding-left: 31%;
			padding-top: 31%;
		}
	}

	.accordion-toggle {
		position: relative;
		display: block;
		padding: 3% 0 3.2% 1.8%;
		text-decoration: none;
		color: @color-gray;
		font-family: 'Montserrat';
		font-size: 1.4rem;
		font-weight: 400;
		text-transform: uppercase;
		overflow: hidden;
	}

	.accordion-content {
		display: none;
		padding: 2.7% 5% 2.7% 2.7%;
		overflow: auto;
		p {
			font-size: 1.5rem;
			font-weight: 300;
			font-style: italic;
			line-height: 2.4rem;
			&:before {
				border-right: 5px solid @color-icon;
				height: 94px;
				content: "";
				width: 1%;
				display: block;
				float: right;
				padding-left: 4%;
			}
		}
	}
}

.accordion-toggle span.toggle-icon {
	position: absolute;
	top: 9px;
	right: 20px;
	font-size: 1.5em;
}

/*--------carousel_1--------*/

#carousel-example-generic,
#carousel-team,
#carousel-work {
		overflow: hidden;
		padding: 3.2% 0 0 0;

	.carousel-inner {
		padding: 0;
		i {
			top: 50%;
		}

		.item {
			.item-p {
				width: 12.6%;
				float: left;
				margin-left: 9%;
				border: 3px solid  @color-icon;
				i {
					padding: 22% 18% 27% 25%;
					color: @color-icon;
					font-size: 7rem;
				}
			}

			.carousel-caption {
				position: static;
				overflow: hidden;
				padding-top: 0;
				padding-left: 5%;
				padding-right: 9%;
				text-align: left;

				p {
					color: @color-text;
					font-size: 2.4rem;
					font-weight: 300;
					font-style: italic;
					line-height: 3.6rem;
					text-shadow: none;

					&:after {
						border-bottom: 3px solid @color-red;
						padding-top: 4%;
						content: "";
						width: 60px;
						margin-left: 0.2%;
						display: block;
					}
				}
				span {
					color: @color-gray;
					font-family: 'Adventure';
					font-size: 2.4rem;
					font-weight: 400;
					position: absolute;
					bottom: 13%;
					left: 32.5%;
				}
			}
		}
	}
}

.carousel-control.left {
	background-image: none;
}

.carousel-control.right {
	background-image: none;
}

