					padding-right: 4%;
					&:hover {
						text-decoration: none;

						span:nth-child(odd) {
							text-decoration: none;
							color: @color-hex;
							transition: 0.5s;
						}
					}
					span:nth-child(odd) {
						color: @color-icon;
